.modal_shop img {
  width: 100px;
  height: 100px;
  margin-right: 5px;
}
.modal_shop .form_shop {
  margin-top: 20px;
}
.modal_shop .img_dec span {
  margin-left: 20px;
  color: red;
  font-size: 18px;
}
.modal_cart .pce {
  display: flex;
  justify-content: flex-end;
  letter-spacing: 1px;
  font-size: 14px;
}
.modal_cart .pce span {
  font-size: 18px;
  color: red;
  margin-left: 10px;
}
.modal_cart .content .unit h6 {
  letter-spacing: 1px;
}
.modal_cart .content .unit h6 span:nth-child(1) {
  color: red;
  font-size: 18px;
}
.modal_cart .content .unit h6 span {
  font-size: 14px;
}
.modal_cart .content .spe,
.modal_cart .content .cor,
.modal_cart .content .num {
  font-size: 15px;
  letter-spacing: 2px;
}
.modal_cart .ant-list-item-meta {
  display: block;
  text-align: center;
}
.modal_cart .ant-list-item-meta span {
  width: 100px;
  height: 100px;
}
.modal_cart .ant-list-item-content {
  justify-content: center;
  margin-top: 10px;
}
.preview .ant-modal-body {
  padding: 0;
}
.logistics h6 {
  font-size: 15px;
  border-bottom: 1px solid #eee;
  margin-top: 10px;
  padding-bottom: 10px;
}
.modal_invoice h6 {
  font-size: 15px;
}
.evaluate_star .star {
  display: flex;
  line-height: 30px;
}
.evaluate_star .star h6 {
  margin-right: 30px;
}
.patMent {
  margin-top: 30px;
}
