.container_details {
  margin: 0 auto;
  width: 500px;
}
.container_details .evaluate_content {
  height: 87px;
  overflow: hidden;
}
.container_details .toggleCentent {
  color: #f31f00;
  margin-top: 5px;
  cursor: pointer;
}
.order_details h6 {
  font-size: 15px;
}
.order_details .order_top {
  border-bottom: 1px solid #ddd;
  margin-bottom: 30px;
  padding-bottom: 30px ;
}
.order_details .order_top h3 {
  font-size: 18px;
}
.order_details .address {
  display: flex;
  padding-bottom: 30px;
  border-bottom: 1px solid #ddd;
}
.order_details .address i svg {
  position: relative;
  top: 12px;
  height: 20px;
  width: 20px;
}
.order_details .address .address_content {
  margin-left: 17px;
}
.order_details .content {
  display: flex;
  margin: 30px 0;
}
.order_details .content .ant-avatar {
  height: 100px;
  width: 100px;
}
.order_details .content .name {
  margin: auto;
  flex: 1 1;
  justify-content: center;
}
.order_details .content .name h6 {
  letter-spacing: 1px;
}
.order_details .content .price {
  margin: auto;
}
.order_details .information {
  padding: 30px 0;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  margin-bottom: 30px;
}
.order_details .information h6 {
  line-height: 2;
  color: rgba(0, 0, 0, 0.8);
}
.order_details .footer {
  display: flex;
  line-height: 30px;
}
.order_details .footer h6 {
  flex: 1 1;
}
.order_details .footer h6:nth-child(2) {
  text-align: right;
}
.details_btn {
  display: flex;
  justify-content: flex-end;
}
.details_btn .againPay {
  margin-left: 10px;
}
.order_allprice span {
  color: red;
}
