.container_change_pwd {
  height: calc(100vh);
  position: relative;
}
.container_change_pwd .change-form {
  background: linear-gradient(265deg, rgba(250, 97, 173, 0.3) 0%, rgba(247, 107, 28, 0.3) 80%);
  padding: 50px 50px 20px 50px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.container_change_pwd .change-form h2 {
  font-weight: normal;
  text-align: center;
  font-size: 17px;
  letter-spacing: 1px;
}
.container_change_pwd .ant-input-affix-wrapper .ant-input {
  min-height: 100%;
  width: 280px;
}
