.container_search {
  width: 100%;
  height: 100vh;
  position: relative;
  background: #f0f2f5;
}
.container_search .ground-container .search_card .ant-card-bordered {
  margin-top: 20px;
}
.container_search .ground-container .search_card .ant-card-bordered .ant-card-body {
  margin-top: 25px;
}
.container_search .ground-container .search_card .ant-card-bordered .ant-card-body .sort_shop {
  padding: 200px 0 30px 0;
}
.container_search .ground-container .ant-select {
  width: 100%;
}
.container_search .ground-container .ant-select-selection--multiple {
  border-radius: 20px;
}
.container_search .ground-container .ant-select-selection--multiple .ant-select-selection__rendered {
  height: 35px;
}
.container_search .ground-container .ant-card-grid {
  width: 23%;
  margin: 1%;
  cursor: pointer;
}
.container_search .ground-container .select_choice {
  width: 100px !important;
  display: inline-block !important;
}
.container_search .search_sort {
  padding: 160px 0 30px 0;
}
.container_search .sort_shop {
  margin-top: 20px;
  text-align: center;
}
.container_search .sort_shop h3 {
  width: 150px;
  display: inline-block;
  font-weight: normal;
}
.container_search .sort_shop h3:hover {
  color: #f36720;
  cursor: pointer;
}
.container_search .shop_selected_bands {
  background-color: #fff;
  margin-top: 20px;
  text-align: left;
  border-bottom: 2px #eee solid;
}
.container_search .shop_selected_bands h2 {
  text-align: center;
}
.container_search .shop_selected_bands .ant-card-bordered {
  display: inline-block;
  margin: 20px 10px;
  height: 360px;
}
.container_search .shop_selected_bands .ant-card-cover img {
  height: 200px;
  border-bottom: 3px #eee solid;
}
.container_search .shop_selected_bands .shop_des {
  text-align: left;
}
.container_search .shop_selected_bands .shop_des .ant-card-meta {
  margin-top: -10px 0 0 0;
}
.container_search .shop_selected_bands .shop_des .ant-card-meta-title {
  color: rgba(255, 0, 0, 0.7);
  letter-spacing: -1px;
}
.container_search .shop_selected_bands .shop_des .ant-card-meta-title span {
  font-size: 15px;
}
.container_search .shop_selected_bands .shop_des .ant-card-meta-title span s {
  color: rgba(0, 0, 0, 0.22);
  font-size: 9px;
  margin-left: 5px;
}
.container_search .shop_selected_bands .shop_des .ant-card-meta-description {
  color: rgba(0, 0, 0, 0.7);
}
.container_search .shop_selected_bands .shop_cart {
  margin-top: 15px;
  display: inline-block;
}
.container_search .shop_selected_bands .shop_cart img {
  width: 32px;
}
.container_search .relevant {
  margin-top: 20px;
  background-color: #fff;
}
.container_search .relevant .ant-card-bordered {
  width: 100px;
  display: inline-block;
  margin: 20px 10px;
  height: 90px;
}
.container_search .relevant .ant-card-bordered img {
  width: 100px;
  height: 90px;
}
.ant-modal-content {
  text-align: c;
  width: 570px;
}
.ant-modal-body .shop_selected_bands {
  margin-top: 20px;
  text-align: left;
  border-bottom: 2px #eee solid;
}
.ant-modal-body .shop_selected_bands h2 {
  text-align: center;
}
.ant-modal-body .shop_selected_bands .ant-card-bordered {
  display: inline-block;
  margin: 20px 9px;
  height: 360px;
}
.ant-modal-body .shop_selected_bands .ant-card-cover img {
  height: 200px;
  border-bottom: 3px #eee solid;
}
.ant-modal-body .shop_selected_bands .shop_des {
  text-align: left;
}
.ant-modal-body .shop_selected_bands .shop_des .ant-card-meta {
  margin-top: -10px 0 0 0;
}
.ant-modal-body .shop_selected_bands .shop_des .ant-card-meta-title {
  color: rgba(255, 0, 0, 0.7);
  letter-spacing: -1px;
}
.ant-modal-body .shop_selected_bands .shop_des .ant-card-meta-title span {
  font-size: 15px;
}
.ant-modal-body .shop_selected_bands .shop_des .ant-card-meta-title span s {
  color: rgba(0, 0, 0, 0.22);
  font-size: 9px;
  margin-left: 5px;
}
.ant-modal-body .shop_selected_bands .shop_des .ant-card-meta-description {
  color: rgba(0, 0, 0, 0.7);
}
.ant-modal-body .shop_selected_bands .shop_cart {
  margin-top: 15px;
  display: inline-block;
}
.ant-modal-body .shop_selected_bands .shop_cart img {
  width: 32px;
}
