.container_table {
  background-color: #fff;
  padding: 0 0  30px 0;
}
.container_table .ant-table-filter-dropdown-btns {
  margin-top: 15px;
}
.container_table .ant-table-filter-dropdown .ant-dropdown-menu {
  border-bottom: 1px #eee solid;
}
.container_table .shop_top {
  padding: 20px 0 0 0;
}
.container_table .shop_top h2 {
  font-weight: normal;
  font-size: 16px;
  text-align: center;
  padding: 5px 0  10px 0 ;
  margin-left: 10px;
  display: inline-block;
}
.container_table .shop_top .ant-input-search {
  display: inline-block;
  width: 500px;
  float: right;
}
.container_table .shop_top .ant-input-affix-wrapper .ant-input:not(:last-child) {
  border-radius: 15px;
}
.container_table .allprice {
  float: right;
  margin-right: 10px;
}
.container_table .allprice span {
  margin-left: 5px;
  color: rgba(248, 0, 0, 0.8);
}
.container_table .shop_cart {
  margin-top: 15px;
  display: inline-block;
}
.container_table .shop_cart img {
  width: 32px;
}
.container_table .ant-input-search ant-input-affix-wrapper {
  height: 37px;
}
.container_table .ant-pagination {
  text-align: center;
  margin-top: 20px;
}
.container_order .ant-table-tbody td:nth-child(7) {
  text-align: center;
}
.container_order .ant-table-tbody td:nth-child(8) {
  text-align: center;
  color: red;
  font-size: 16px;
}
