.login-form {
  max-width: 300px;
}
.login-form-forgot a {
  float: right;
}
.login-form-button {
  width: 280px;
}
.container_login .content .logo-top {
  height: 100px;
}
.container_login .content .logo-top img {
  width: 50px;
}
.container_login .content .logo-top h1 {
  display: inline-block;
  font-size: 36px;
  position: relative;
  top: 8px;
  left: 50px;
  letter-spacing: 3px;
  color: #fff;
}
.container_login .content .ant-tabs {
  width: 280px;
}
.container_login .content .ant-tabs .ant-tabs-bar {
  width: 280px;
}
.container_login .content .ant-tabs .ant-tabs-nav-wrap {
  text-align: center;
}
.container_login .content .ant-tabs .ant-input:not(:first-child) {
  height: 36px;
}
.container_login .content .ant-tabs .btn_280 {
  width: 280px;
  display: block;
}
.container_login .content .ant-input-affix-wrapper .ant-input:not(:first-child) {
  width: 280px;
}
.container_login .content .register {
  float: right;
}
