/** 常用色值 **/
/** 常用字体大小 **/
.component_header {
  border-radius: 35px;
  height: 60px;
  margin-top: 20px;
  min-width: 500px;
  color: #fff;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
  background: linear-gradient(265deg, rgba(250, 97, 173, 0.8) 0%, #f76b1c 80%);
}
.component_header .header-top {
  margin: 0 auto;
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
}
.component_header .header-top .header-top-nav {
  text-align: right;
  cursor: pointer;
}
.component_header .header-top .header-top-nav .header-avatar {
  margin: 0 10px 0 0;
}
.component_header .header-top .header-top-nav span {
  font-weight: bold;
}
.component_header .header-top h3 {
  letter-spacing: 1px;
  min-width: 140px;
  color: #ddd;
}
.component_header .header-top h3 span {
  margin-left: 10px;
  font-size: 13px;
}
.component_header .breadcrumb {
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
}
.component_header .breadcrumb .breadcrumb-title {
  text-align: center;
  font-size: 18px;
}
.component_header .breadcrumb .breadcrumb-title:after {
  position: absolute;
  content: '';
  left: 73px;
  top: 39px;
  border-top: 9px solid #ffffff;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
}
.component_header .breadcrumb .weather {
  text-align: right;
  font-size: 14px;
}
.component_header .breadcrumb .weather .date {
  margin-right: 10px;
  vertical-align: middle;
}
.component_header .breadcrumb .weather .weather-img img {
  height: 15px;
}
.component_header .breadcrumb .weather .weather-detail {
  margin-left: 5px;
  vertical-align: middle;
}
.simple-page .header-top {
  background: #1890ff;
  color: #ffffff;
}
