.container_shop {
  min-width: 1100px;
  background-color: #fff;
}
.container_shop .shop_top .ant-input-search {
  display: inline-block;
  width: 500px;
  float: right;
  margin-right: 10px;
}
.container_shop .shop_top .ant-input-affix-wrapper .ant-input:not(:last-child) {
  border-radius: 15px;
}
.container_shop .allprice {
  float: right;
  margin-right: 10px;
}
.container_shop .allprice span {
  margin-left: 5px;
  color: rgba(248, 0, 0, 0.8);
}
.container_shop .shop_cart {
  margin-top: 15px;
  display: inline-block;
}
.container_shop .shop_cart img {
  width: 32px;
}
.container_shop .ant-input-search ant-input-affix-wrapper {
  height: 37px;
}
.container_shop .ant-table-tbody td:nth-child(5) {
  color: red;
  font-size: 16px;
}
.container_shop .ant-table-tbody td:nth-child(6) {
  color: red;
  font-size: 16px;
}
.container_shop .ant-avatar-image {
  height: 60px;
  width: 60px;
}
