.container_lr {
  width: 100%;
  height: 100vh;
  position: relative;
  background: #f0f2f5;
}
.container_lr .ground-container {
  height: calc(100vh);
  width: 100%;
}
.container_lr .ant-carousel .slick-slide {
  height: calc(100vh) !important;
  line-height: 160px;
}
.container_lr .ant-carousel .slick-slide img {
  color: #fff;
  height: calc(100vh);
}
