.selected_bands {
  background-color: #fff;
  padding: 35px 0 50px 0 ;
  margin-top: 20px;
  text-align: center;
}
.selected_bands h2 {
  text-align: center;
}
.selected_bands .ant-card-bordered {
  display: inline-block;
  margin: 20px 10px;
  height: 310px;
}
.selected_bands .ant-card-cover img {
  height: 200px;
  border-bottom: 3px #eee solid;
}
.selected_bands .shop_des {
  text-align: left;
}
.selected_bands .shop_des .ant-card-meta {
  margin-top: -10px 0 0 0;
}
.selected_bands .shop_des .ant-card-meta-title {
  color: rgba(255, 0, 0, 0.7);
  letter-spacing: -1px;
}
.selected_bands .shop_des .ant-card-meta-title span {
  font-size: 15px;
}
.selected_bands .shop_des .ant-card-meta-title span s {
  color: rgba(0, 0, 0, 0.22);
  font-size: 9px;
  margin-left: 5px;
}
.selected_bands .shop_des .ant-card-meta-description {
  color: rgba(0, 0, 0, 0.7);
}
