.modal_order h6 {
  font-size: 15px;
}
.modal_order .address {
  display: flex;
}
.modal_order .address i svg {
  position: relative;
  top: 12px;
  height: 20px;
  width: 20px;
}
.modal_order .address .address_content {
  margin-left: 17px;
}
.modal_order .order_inf {
  margin: 20px 0;
  padding: 20px 0;
  border-bottom: 1px solid #ddd;
  border-top: 1px solid #ddd;
}
.modal_order .order_inf h6:nth-child(3) {
  font-size: 14px;
  color: #999;
  letter-spacing: 1px;
}
.modal_order .order_inf h6 span {
  color: red;
}
.modal_pay .order_content,
.modal_cancel .order_content,
.modal_invoice .order_content {
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal_pay .order_content h6,
.modal_cancel .order_content h6,
.modal_invoice .order_content h6 {
  font-size: 15px;
}
.modal_pay .order_content .pictures img,
.modal_cancel .order_content .pictures img,
.modal_invoice .order_content .pictures img {
  height: 100px;
  width: 100px;
}
.modal_pay .order_content .name_spe,
.modal_cancel .order_content .name_spe,
.modal_invoice .order_content .name_spe {
  flex: 1 1;
}
.modal_pay .order_content .price_number h6 span,
.modal_cancel .order_content .price_number h6 span,
.modal_invoice .order_content .price_number h6 span {
  font-size: 18px;
  color: red;
}
.refund_information {
  border: none !important;
}
