.nav-menu {
  min-width: 180px;
  background: linear-gradient(265deg, rgba(250, 97, 173, 0.8) 0%, #f76b1c 80%);
}
.nav-menu .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 60px;
  padding-left: 20px;
}
.nav-menu .logo img {
  height: 35px;
}
.nav-menu .logo h1 {
  color: #ffffff;
  font-size: 20px;
  display: inline-block;
  vertical-align: middle;
  margin: 0 0 0 10px;
  letter-spacing: 1px;
}
.nav-menu .logo .ant-menu-submenu-title {
  line-height: 50px;
  height: 50px;
}
.nav-menu .menuList {
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-menu .menuList .ant-menu-dark {
  background: linear-gradient(265deg, rgba(250, 97, 173, 0.8) 0%, #f76b1c 80%);
  line-height: 60px;
}
.ant-menu-item-selected {
  color: red;
}
.ant-menu.ant-menu-dark .ant-menu-item-selected,
.ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected {
  background: rgba(255, 0, 0, 0.6);
}
.ant-menu-dark .ant-menu-sub {
  background: linear-gradient(265deg, rgba(250, 97, 173, 0.8) 0%, #f76b1c 80%);
}
